  display: table;
  clear: both;
}
.dropdown__form .input {
  float: left;
  width: 170px;
  margin-right: 10px;
}
.dropdown__form .btn {
  float: left;
}
.dropdown_coupon-info {
  background: url('../img/coupon_info_popup.png') 100% bottom no-repeat;
  width: 407px;
  height: 269px;
  color: #fff;
  text-align: center;
  height: 170px;
  left: 0;
}
.dropdown_coupon-info .dropdown__text {
  font-size: 13px;
  position: relative;
  padding-top: 30px;
  bottom: 0;
  display: inline-block;
  line-height: 20px;
