}
.tooltip {
  position: absolute;
  z-index: 999;
  width: 2.2em;
  height: 2.2em;
  cursor: pointer;
}
.tooltip:hover .tooltip__item {
  border-color: #fdeb38;
}
.tooltip_gorizontal {
  top: 250px;
  left: 65%;
}
.tooltip_wood {
  top: 670px;
  left: 27%;
}
.tooltip__item {
  width: 27px;
  height: 27px;
  left: 50%;
  top: 50%;
  margin: -15px 0 0 -15px;
  position: absolute;
  border-radius: 50%;
  border: 3px solid #fdeb38;
  animation: pulse 0.6s infinite alternate;
}
.tooltip__item:before {
  content: '+';
  position: absolute;
  color: #fdeb38;
  font-size: 25px;
  font-weight: 700;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  line-height: 1;
}
.tooltip__content {
  position: absolute;
  background: #ffffff;
  z-index: 9999;
  width: 250px;
  bottom: 50%;
  margin-bottom: -1em;
  padding: 20px;
  border-radius: 10px;
  font-size: 15px;
  text-align: center;
  color: #3a393a;
  opacity: 0;
  cursor: default;
  pointer-events: none;
  -webkit-font-smoothing: antialiased;
  -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s;
}
.tooltip__buttons {
  display: inline-block;
  margin-top: 20px;
}
.tooltip__buttons .btn {
  width: 170px;
  margin-bottom: 10px;
}
.tooltip__buttons .btn:last-child {
  margin-bottom: 0;
}
.tooltip__buttons .btn_green {
  text-transform: uppercase;
}
@keyframes pulse {
  from {
    -webkit-transform: scale3d(0.5, 0.5, 1);
    transform: scale3d(0.5, 0.5, 1);
  }
  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}
.tooltip-west .tooltip__content {
  left: 4em;
  -webkit-transform-origin: -2em 50%;
  transform-origin: -2em 50%;
  -webkit-transform: translate3d(0, 50%, 0) rotate3d(1, 1, 1, 30deg);
  transform: translate3d(0, 50%, 0) rotate3d(1, 1, 1, 30deg);
}
.tooltip-east .tooltip__content {
  right: 4em;
  -webkit-transform-origin: calc(102%) 50%;
  transform-origin: calc(102%) 50%;
  -webkit-transform: translate3d(0, 50%, 0) rotate3d(1, 1, 1, -30deg);
  transform: translate3d(0, 50%, 0) rotate3d(1, 1, 1, -30deg);
}
.tooltip:hover .tooltip__content {
  opacity: 1;
  -webkit-transform: translate3d(0, 50%, 0) rotate3d(0, 0, 0, 0);
  transform: translate3d(0, 50%, 0) rotate3d(0, 0, 0, 0);
  pointer-events: auto;
}
/* Gap "bridge" and arrow */
.tooltip__content::before,
.tooltip__content::after {
  content: '';
  position: absolute;
}
.tooltip__content::before {
  height: 100%;
  width: 3em;
}
.tooltip__content::after {
  width: 2em;
  height: 2em;
  top: 50%;
  margin: -1em 0 0;
  background: url('data:image/svg+xml,%3Csvg%20version%3D%221.1%22%20id%3D%22Layer_1%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20xmlns%3Axlink%3D%22http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink%22%20x%3D%220px%22%20y%3D%220px%22%20viewBox%3D%220%200%2080%2080%22%20enable-background%3D%22new%200%200%2080%2080%22%20xml%3Aspace%3D%22preserve%22%3E%3Cpath%20fill%3D%22#ffffff%22%20d%3D%22M2%2C34c0%2C0%2C58.2%2C40%2C78%2C46V27C80%2C27%2C45.2%2C49.8%2C2%2C34z%22%2F%3E%3C%2Fsvg%3E') no-repeat center center;
  background-size: 100%;
}
.tooltip-west .tooltip__content::before,
.tooltip-west .tooltip__content::after {
  right: 99%;
  /* because of FF, otherwise we have a gap */
}
.tooltip-east .tooltip__content::before,
.tooltip-east .tooltip__content::after {
  left: 99%;
  /* because of FF, otherwise we have a gap */
}
.tooltip-east .tooltip__content::after {
  -webkit-transform: scale3d(-1, 1, 1);
  transform: scale3d(-1, 1, 1);
}
.input {
  color: #2c2b29;
  border-radius: 3px;
  border: none;
  height: 33px;
  padding: 5px 12px;
}
.coupon {
  position: absolute;
  width: 30px;
  height: 30px;
  background: orange;
  border-radius: 50%;
  z-index: 5;
}
.coupon.collected {
  background: green;
}
@media (max-width: 1024px) {
  .tooltip_gorizontal {
    top: 200px;
    left: 60%;
  }
  .tooltip_wood {
    top: 530px;
    left: 33%;
  }
}
